webpackJsonp([184622575581849],{

/***/ 644:
/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"id":"E:/gatsby-starter-personal-blog/content/posts/2017-08-15--gatsby-blog-starter/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/photo-34475132951_e3ff74c679_o-7294fb5ced339f950b55bf17abc3b01e-72e4a.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.400000000000006%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"Harbin, China\"\n        title=\"\"\n        src=\"/static/photo-34475132951_e3ff74c679_o-7294fb5ced339f950b55bf17abc3b01e-48538.png\"\n        srcset=\"/static/photo-34475132951_e3ff74c679_o-7294fb5ced339f950b55bf17abc3b01e-bed0f.png 200w,\n/static/photo-34475132951_e3ff74c679_o-7294fb5ced339f950b55bf17abc3b01e-5fd34.png 400w,\n/static/photo-34475132951_e3ff74c679_o-7294fb5ced339f950b55bf17abc3b01e-48538.png 800w,\n/static/photo-34475132951_e3ff74c679_o-7294fb5ced339f950b55bf17abc3b01e-b70ec.png 1200w,\n/static/photo-34475132951_e3ff74c679_o-7294fb5ced339f950b55bf17abc3b01e-72e4a.png 1500w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<!-- TOC -->\n<ul>\n<li><a href=\"#01-gatsby-source-filesystem\">01 gatsby-source-filesystem</a></li>\n<li><a href=\"#02-gatsby-transformer-remark\">02 gatsby-transformer-remark</a></li>\n<li><a href=\"#03-creating-a-first-blog-post\">03 Creating a first Blog Post</a></li>\n<li><a href=\"#04-creating-an-index-page\">04 Creating an Index Page</a></li>\n<li><a href=\"#05-filters--sorting-with-graphql\">05 Filters &#x26; Sorting With GraphQL</a></li>\n<li><a href=\"#06-static-serve\">06 Static Serve</a></li>\n</ul>\n<!-- /TOC -->\n<p>For an overview of the project structure please refer to the <a href=\"https://www.gatsbyjs.org/docs/building-with-components/\">Gatsby documentation - Building with Components</a></p>\n<p>Install this starter (assuming Gatsby is installed) by running from your CLI:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">gatsby new gatsby-blog</code></pre>\n      </div>\n<p>This is the second mini-project that I am going to use Gatsby in - check out the <a href=\"https://github.com/mpolinowski/gatsby-wiki\">first one</a> for a more detailed description of all the basics steps of setting up Gatsby.</p>\n<ol>\n<li><a href=\"#01-gatsby-source-filesystem\">gatsby-source-filesystem</a></li>\n<li><a href=\"#02-gatsby-transformer-remark\">gatsby-transformer-remark</a></li>\n<li><a href=\"#03-creating-a-first-blog-post\">Creating a first Blog Post</a></li>\n<li><a href=\"#04-creating-an-index-page\">Creating an Index Page</a></li>\n<li><a href=\"#05-filters--sorting-with-graphql\">Filters &#x26; Sorting With GraphQL</a></li>\n<li><a href=\"#06-static-serve\">Static Serve</a></li>\n</ol>\n<h2 id=\"01-gatsby-source-filesystem\"><a href=\"#01-gatsby-source-filesystem\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>01 gatsby-source-filesystem</h2>\n<p>First we install a plugin that allows us to pull in data. There are a couple of <a href=\"https://www.gatsbyjs.org/docs/plugins/\">Source Plugins</a> available to get your data from MongoDB or a CMS source like Wordpress. But we only want to grab Markdown files from our local file system -> hence we are going to install <a href=\"https://www.gatsbyjs.org/packages/gatsby-source-filesystem/\">gatsby-source-filesystem</a>, using npm:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token function\">npm</span> <span class=\"token function\">install</span> --save gatsby-source-filesystem</code></pre>\n      </div>\n<p>The plugin then has to be added to our Gatsby build by adding it to /gatsby-config.js. It is added as an object, carrying some configuration - in this case the path to the directory that will store our Markdown files:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\">plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token template-string\"><span class=\"token string\">`gatsby-plugin-react-helmet`</span></span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">{</span>\n  resolve<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`gatsby-source-filesystem`</span></span><span class=\"token punctuation\">,</span>\n  options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    name<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`pages`</span></span><span class=\"token punctuation\">,</span>\n    path<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>__dirname<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">/src/pages/`</span></span><span class=\"token punctuation\">,</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span></code></pre>\n      </div>\n<h2 id=\"02-gatsby-transformer-remark\"><a href=\"#02-gatsby-transformer-remark\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>02 gatsby-transformer-remark</h2>\n<p>Since we want to use Markdown as a data source, we need a transformer plugin, that turns the Markdown into something that GraphQL can query against. This is the <a href=\"https://www.gatsbyjs.org/packages/gatsby-transformer-remark/\">gatsby-transformer-remark</a> plugin, that can also be installed by npm:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token function\">npm</span> <span class=\"token function\">install</span> --save gatsby-transformer-remark</code></pre>\n      </div>\n<p>This plugin comes without any additional configuration, and can simply be added to gatsby-config.js:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\">plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token template-string\"><span class=\"token string\">`gatsby-transformer-remark`</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span></code></pre>\n      </div>\n<p>Now we can start the Gatsby development environment with:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">gatsby develop</code></pre>\n      </div>\n<p>The app will start on <a href=\"http://localhost:8000\">http://localhost:8000</a></p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/gatsby_01-d4dfcc82f74a6291cdcd26fe67f5871d-31dfd.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 33.685220729366605%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"gatsby 01\"\n        title=\"\"\n        src=\"/static/gatsby_01-d4dfcc82f74a6291cdcd26fe67f5871d-48538.png\"\n        srcset=\"/static/gatsby_01-d4dfcc82f74a6291cdcd26fe67f5871d-bed0f.png 200w,\n/static/gatsby_01-d4dfcc82f74a6291cdcd26fe67f5871d-5fd34.png 400w,\n/static/gatsby_01-d4dfcc82f74a6291cdcd26fe67f5871d-48538.png 800w,\n/static/gatsby_01-d4dfcc82f74a6291cdcd26fe67f5871d-31dfd.png 1042w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2 id=\"03-creating-a-first-blog-post\"><a href=\"#03-creating-a-first-blog-post\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>03 Creating a first Blog Post</h2>\n<p>Now we want to prepare our first blog post. We can add a folder to the pages directory with the date of today ~ as this might make sense to a blog (naming convention is up to you) -> <em>/src/pages/2017-10-05-first-post</em>. Now add a new file to this folder, named <em>index.md</em>. The Markdown file needs some information at the top of the file - called <a href=\"https://jekyllrb.com/docs/frontmatter/\">FrontMatter</a>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\">---\npath<span class=\"token operator\">:</span> <span class=\"token string\">\"/first-post\"</span>\ntitle<span class=\"token operator\">:</span> <span class=\"token string\">\"First Blog Post\"</span>\ndate<span class=\"token operator\">:</span>   <span class=\"token string\">\"2017-10-05\"</span>\n---</code></pre>\n      </div>\n<p>This <a href=\"https://jekyllrb.com/docs/frontmatter/\">FrontMatter</a> will be used to Query against with GraphQL and can carry a couple of different information - see also <a href=\"https://github.com/mpolinowski/gatsby-wiki#11-working-with-markdown\">my previous repo</a>. But we only use it to add an title to our blogpost and assign an URL under which we want to be able to access the file. Now just add some Markdown to the file and save it:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">## Hello from Markdown!\n---\n\n### This is a first blog Post\n\nHow exciting Web Development can be, amazing!</code></pre>\n      </div>\n<p>The markdown represents the data that is going to be displayed. But now we need to create a style template that is used with this data. Lets start by adding a new folder inside /src called <em>templates</em>. Now add a file to it called <strong>post.js</strong> that will contain the structure template for every post entry. The file contains the JSX markup for our post:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">import</span> React <span class=\"token keyword\">from</span> <span class=\"token string\">'react'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> Helmet <span class=\"token keyword\">from</span> <span class=\"token string\">'react-helmet'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token keyword\">function</span> <span class=\"token function\">Template</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>data<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> <span class=\"token punctuation\">{</span>markdownRemark<span class=\"token punctuation\">:</span> post<span class=\"token punctuation\">}</span> <span class=\"token operator\">=</span> data<span class=\"token punctuation\">;</span>\n  <span class=\"token comment\">// const post = data.markdownRemark;</span>\n  <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n      <span class=\"token operator\">&lt;</span>h1<span class=\"token operator\">></span><span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>frontmatter<span class=\"token punctuation\">.</span>title<span class=\"token punctuation\">}</span><span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>h1<span class=\"token operator\">></span>\n      <span class=\"token operator\">&lt;</span>div dangerouslySetInnerHTML<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>__html<span class=\"token punctuation\">:</span> post<span class=\"token punctuation\">.</span>html<span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span> <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n  <span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>The &#x3C;Template /> component receives {data} props, that are retrieved by an GraphQL query:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">export</span> <span class=\"token keyword\">const</span> postQuery <span class=\"token operator\">=</span> graphql<span class=\"token template-string\"><span class=\"token string\">`\n  query BlogPostByPath($path: String!) {\n    markdownRemark(frontmatter: { path: { eq: $path} }) {\n      html\n      frontmatter {\n        path\n        title\n      }\n    }\n  }\n`</span></span></code></pre>\n      </div>\n<p>The query looks for a markdown post, where the called URL equals the $path given inside it’s frontmatter. So if the URL that you type into your browser was <em>/first-post</em>, a markdown file with a <em>path: ‘/first-post’</em> inside it’s frontmatter, would be a hit.</p>\n<p>The query then uses the markdownRemark plugin to transform the post markdown to HTML and make both the path and title from it’s frontmatter available iside {data}, that is passed down into the &#x3C;Template /> component and then rendered.</p>\n<p>Gatsby is already configured to route all pages inside /src/pages as pages for our website. But now we have to register our posts, that are from the markdown files and the post.js template. To do this, we have to create a file named <strong>gatsby-node.js</strong> inside the root directory of our app. We are going to use the <a href=\"https://www.gatsbyjs.org/docs/node-apis/#createPages\">createPages</a> Gatsby API to create pages from our post template:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> path <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'path'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nexports<span class=\"token punctuation\">.</span><span class=\"token function-variable function\">createPages</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>boundActionCreators<span class=\"token punctuation\">,</span> graphql<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> <span class=\"token punctuation\">{</span>createPage<span class=\"token punctuation\">}</span> <span class=\"token operator\">=</span> boundActionCreators<span class=\"token punctuation\">;</span>\n  <span class=\"token comment\">// const createPage = boundActionCreators.createPage;</span>\n\n  <span class=\"token keyword\">const</span> postTemplate <span class=\"token operator\">=</span> path<span class=\"token punctuation\">.</span><span class=\"token function\">resolve</span><span class=\"token punctuation\">(</span><span class=\"token string\">'src/templates/post.js'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">return</span> <span class=\"token function\">graphql</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`{\n    allMarkdownRemark {\n      edges {\n        node {\n          html\n          id\n          frontmatter {\n            path\n            title\n          }\n        }\n      }\n    }\n  }`</span></span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">.</span><span class=\"token function\">then</span><span class=\"token punctuation\">(</span>res <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">if</span><span class=\"token punctuation\">(</span>res<span class=\"token punctuation\">.</span>errors<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">return</span> Promise<span class=\"token punctuation\">.</span><span class=\"token function\">reject</span><span class=\"token punctuation\">(</span>res<span class=\"token punctuation\">.</span>errors<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    res<span class=\"token punctuation\">.</span>data<span class=\"token punctuation\">.</span>allMarkdownRemark<span class=\"token punctuation\">.</span>edges<span class=\"token punctuation\">.</span><span class=\"token function\">forEach</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>node<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n      <span class=\"token function\">createPage</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n        path<span class=\"token punctuation\">:</span> node<span class=\"token punctuation\">.</span>frontmatter<span class=\"token punctuation\">.</span>path<span class=\"token punctuation\">,</span>\n        component<span class=\"token punctuation\">:</span> postTemplate\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Save and restart your app - then open <strong><a href=\"http://localhost:8000/first-post\">http://localhost:8000/first-post</a></strong> inside your web browser:</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/gatsby_02-9189fc01c2ebbb345affa14bebc77299-f6b03.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 33.84467881112177%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"gatsby 02\"\n        title=\"\"\n        src=\"/static/gatsby_02-9189fc01c2ebbb345affa14bebc77299-48538.png\"\n        srcset=\"/static/gatsby_02-9189fc01c2ebbb345affa14bebc77299-bed0f.png 200w,\n/static/gatsby_02-9189fc01c2ebbb345affa14bebc77299-5fd34.png 400w,\n/static/gatsby_02-9189fc01c2ebbb345affa14bebc77299-48538.png 800w,\n/static/gatsby_02-9189fc01c2ebbb345affa14bebc77299-f6b03.png 1043w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2 id=\"04-creating-an-index-page\"><a href=\"#04-creating-an-index-page\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>04 Creating an Index Page</h2>\n<p>Now that we have a blog post (you can duplicate the first one a couple of times - changing the title, path and date), we will need an index page with a collection of links for all our posts. We will use the same GraphQL query used above to create this list.</p>\n<p>We can add optional sorting, limiting parameters for the GraphQL query - see also <a href=\"https://github.com/graphql/graphiql/issues/587\">1</a>, <a href=\"https://gist.github.com/DSchau/86ee9288b05d236dada81148f66db8db\">2</a>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token function\">allMarkdownRemark</span><span class=\"token punctuation\">(</span>sort<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> fields<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>frontmatter___title<span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> order<span class=\"token punctuation\">:</span> <span class=\"token constant\">ASC</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token function\">allMarkdownRemark</span><span class=\"token punctuation\">(</span>sort<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> fields<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>frontmatter___date<span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> order<span class=\"token punctuation\">:</span> <span class=\"token constant\">DESC</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token function\">allMarkdownRemark</span><span class=\"token punctuation\">(</span>limit<span class=\"token punctuation\">:</span> <span class=\"token number\">1000</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>Gatsby comes with the GraphiQL debugger that allows us to test queries before we add them to our page - the debugger is running under: <strong><a href=\"http://localhost:8000/___graphql\">http://localhost:8000/___graphql</a></strong></p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/gatsby_03-345f3369a9f31f67698f77737dd044cf-f6b03.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.15819750719079%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"gatsby 03\"\n        title=\"\"\n        src=\"/static/gatsby_03-345f3369a9f31f67698f77737dd044cf-48538.png\"\n        srcset=\"/static/gatsby_03-345f3369a9f31f67698f77737dd044cf-bed0f.png 200w,\n/static/gatsby_03-345f3369a9f31f67698f77737dd044cf-5fd34.png 400w,\n/static/gatsby_03-345f3369a9f31f67698f77737dd044cf-48538.png 800w,\n/static/gatsby_03-345f3369a9f31f67698f77737dd044cf-f6b03.png 1043w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token punctuation\">{</span>\n  <span class=\"token function\">allMarkdownRemark</span><span class=\"token punctuation\">(</span>limit<span class=\"token punctuation\">:</span> <span class=\"token number\">10</span><span class=\"token punctuation\">,</span> sort<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>fields<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>frontmatter___date<span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> order<span class=\"token punctuation\">:</span> <span class=\"token constant\">DESC</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    edges <span class=\"token punctuation\">{</span>\n      node <span class=\"token punctuation\">{</span>\n        frontmatter <span class=\"token punctuation\">{</span>\n          path\n          title\n          date\n        <span class=\"token punctuation\">}</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>This query shows the latest 10 Markdown posts in descending order - let’s add it to our index page <em>/src/pages/index.js</em>. First we add the GraphQL query, below the &#x3C;IndexPage /> component:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">export</span> <span class=\"token keyword\">const</span> pageQuery <span class=\"token operator\">=</span> graphql<span class=\"token template-string\"><span class=\"token string\">`\n  query IndexQuery {\n      allMarkdownRemark(limit: 10\n      sort: {fields: [frontmatter___date], order: DESC}\n    ) {\n      edges {\n        node {\n          id\n          frontmatter {\n            path\n            title\n            date\n          }\n        }\n      }\n    }\n  }\n`</span></span></code></pre>\n      </div>\n<p>Then we inject the data from that query into the &#x3C;IndexPage /> component and loop through it to generate our index:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">IndexPage</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>data<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span>\n  <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span>h1<span class=\"token operator\">></span>Hi people<span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>h1<span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span>p<span class=\"token operator\">></span>Welcome to your <span class=\"token keyword\">new</span> <span class=\"token class-name\">Gatsby</span> site<span class=\"token punctuation\">.</span><span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>p<span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span>p<span class=\"token operator\">></span>Now go build something great<span class=\"token punctuation\">.</span><span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>p<span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span>br<span class=\"token operator\">/</span><span class=\"token operator\">></span><span class=\"token operator\">&lt;</span>br<span class=\"token operator\">/</span><span class=\"token operator\">></span>\n\n    <span class=\"token operator\">&lt;</span>Link to<span class=\"token operator\">=</span><span class=\"token string\">\"/page-2/\"</span><span class=\"token operator\">></span>Go to page <span class=\"token number\">2</span><span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>Link<span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span>br<span class=\"token operator\">/</span><span class=\"token operator\">></span><span class=\"token operator\">&lt;</span>br<span class=\"token operator\">/</span><span class=\"token operator\">></span><span class=\"token operator\">&lt;</span>br<span class=\"token operator\">/</span><span class=\"token operator\">></span><span class=\"token operator\">&lt;</span>br<span class=\"token operator\">/</span><span class=\"token operator\">></span>\n\n    <span class=\"token operator\">&lt;</span>h2<span class=\"token operator\">></span>Index<span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>h2<span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span>table<span class=\"token operator\">></span>\n      <span class=\"token operator\">&lt;</span>thead<span class=\"token operator\">></span>\n        <span class=\"token operator\">&lt;</span>tr<span class=\"token operator\">></span>\n          <span class=\"token operator\">&lt;</span>th<span class=\"token operator\">></span>Date<span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>th<span class=\"token operator\">></span>\n          <span class=\"token operator\">&lt;</span>th<span class=\"token operator\">></span>Link<span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>th<span class=\"token operator\">></span>\n        <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>tr<span class=\"token operator\">></span>\n      <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>thead<span class=\"token operator\">></span>\n      <span class=\"token operator\">&lt;</span>tbody<span class=\"token operator\">></span>\n      <span class=\"token punctuation\">{</span>data<span class=\"token punctuation\">.</span>allMarkdownRemark<span class=\"token punctuation\">.</span>edges<span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span>post <span class=\"token operator\">=></span> <span class=\"token punctuation\">(</span>\n        <span class=\"token operator\">&lt;</span>tr key<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>node<span class=\"token punctuation\">.</span>id<span class=\"token punctuation\">}</span><span class=\"token operator\">></span>\n          <span class=\"token operator\">&lt;</span>td<span class=\"token operator\">></span>\n            <span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>node<span class=\"token punctuation\">.</span>frontmatter<span class=\"token punctuation\">.</span>date<span class=\"token punctuation\">}</span>\n          <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>td<span class=\"token operator\">></span>\n          <span class=\"token operator\">&lt;</span>td<span class=\"token operator\">></span>\n            <span class=\"token operator\">&lt;</span>Link\n              to<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>node<span class=\"token punctuation\">.</span>frontmatter<span class=\"token punctuation\">.</span>path<span class=\"token punctuation\">}</span><span class=\"token operator\">></span>\n              <span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>node<span class=\"token punctuation\">.</span>frontmatter<span class=\"token punctuation\">.</span>title<span class=\"token punctuation\">}</span>\n            <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>Link<span class=\"token operator\">></span>\n          <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>td<span class=\"token operator\">></span>\n        <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>tr<span class=\"token operator\">></span>\n      <span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span>\n      <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>tbody<span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>table<span class=\"token operator\">></span>\n  <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n<span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/gatsby_04-ba9f25179ee1df5721759110b9120a46-f6b03.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 74.68839884947268%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"gatsby 04\"\n        title=\"\"\n        src=\"/static/gatsby_04-ba9f25179ee1df5721759110b9120a46-48538.png\"\n        srcset=\"/static/gatsby_04-ba9f25179ee1df5721759110b9120a46-bed0f.png 200w,\n/static/gatsby_04-ba9f25179ee1df5721759110b9120a46-5fd34.png 400w,\n/static/gatsby_04-ba9f25179ee1df5721759110b9120a46-48538.png 800w,\n/static/gatsby_04-ba9f25179ee1df5721759110b9120a46-f6b03.png 1043w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2 id=\"05-filters--sorting-with-graphql\"><a href=\"#05-filters--sorting-with-graphql\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>05 Filters &#x26; Sorting With GraphQL</h2>\n<p>We already sorted our results by date and name in the examples above. Now let’s add another value to our frontmatter, to decide whether a post is a draft or should already be shown as published. Set the first post to <em>published: true</em> and the second one to <em>published: false</em> :</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">---\npath: &#39;/second-post&#39;\ntitle: &#39;Second Blog Post&#39;\ndate:   &quot;2017-10-07&quot;\npublished: false\n---</code></pre>\n      </div>\n<p>Now we just need to configure our GraphQL query to only show post where <em>published</em> is set to true:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">export</span> <span class=\"token keyword\">const</span> pageQuery <span class=\"token operator\">=</span> graphql<span class=\"token template-string\"><span class=\"token string\">`\n  query IndexQuery {\n    allMarkdownRemark(\n      limit: 10\n      sort: {fields: [frontmatter___date], order: DESC}\n      filter: { frontmatter: { published: {eq: true} }}\n    ) {\n      edges {\n        node {\n          id\n          frontmatter {\n            path\n            title\n            date\n            published\n          }\n        }\n      }\n    }\n  }\n`</span></span></code></pre>\n      </div>\n<h2 id=\"06-static-serve\"><a href=\"#06-static-serve\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>06 Static Serve</h2>\n<p>To serve our website we can use the command:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">gatsby build</code></pre>\n      </div>\n<p>This will build a static version of our React app inside /public. Just upload it to a webserver or to GitHub Pages and you are good to go.</p>\n<p><strong>After</strong> building the static page, you can also use the command:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">gatsby serve</code></pre>\n      </div>\n<p>That - unlike our development environment - fires up the webpack server in production mode - that means the files that are served, are the optimized files from the <em>/public</em> directory! You can access this version on <em><a href=\"http://localhost:9000\">http://localhost:9000</a></em></p>","fields":{"slug":"/gatsby-blog-starter/","prefix":"2017-08-15"},"frontmatter":{"title":"Gatsby Blog Starter","subTitle":"Build a JAM Stack Blog with Gatsby.js","cover":{"childImageSharp":{"resize":{"src":"/static/photo-34475132951_e3ff74c679_o-cover-a009a0f7b9a619642382b5ded16773f5-160fa.png"}}}}},"author":{"id":"E:/gatsby-starter-personal-blog/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mike Polinowski</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/contact/\">contact me</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. :hearts:</p>"},"footnote":{"id":"E:/gatsby-starter-personal-blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>This is a personal Developer Blog</li>\n<li>Ξ</li>\n<li>built by <a href=\"https://github.com/mpolinowski\">Mike Polinowski</a></li>\n<li>Ξ</li>\n<li>with <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> &#x26; <a href=\"https://reactjs.org\">React</a></li>\n<li>Ξ</li>\n<li>source code on <a href=\"https://github.com/mpolinowski/mpolinowski.github.io\">GitHub</a></li>\n<li>Ξ</li>\n<li>deliverd by <a href=\"https://pages.github.com\">Github Pages</a></li>\n<li>Ξ</li>\n<li>photos from <a href=\"https://www.flickr.com/people/149680084@N06/\">Flickr</a></li>\n</ul>"}},"pathContext":{"slug":"/gatsby-blog-starter/"}}

/***/ })

});


